<svelte:head>
    <title>TODH</title>
</svelte:head>

<Content>
    <ContentArea>

        <Area title="TODH en el prototipado Frontend">
        <p><strong>Veamos un prototipo Frontend en alta definicin (este mismo sitio es un ejemplo), y vemoslo desde sus 4 dimensiones bsicas: </strong>
            <Tag tag="Javascript" isCode={true} /> (motor),
            <Tag tag="CSS" isCode={true} /> (apariencia),
            <Tag isCode={true} tag="Html" /> (estructura) y
            <Tag tag="Json" isCode={true} /> (contenidos).</p>
        <p>Como venimos viendo en TODH cada una de estas dimensiones tiene una funcin, unas cualidades, una energa...
            que unidas constituyen un "todo" que es <em>ms que la suma de sus partes</em>. Tratar de que veamos cmo
            hasta cuando desarrollamos productos digitales seguimos unas pautas, un ordenamiento y unas directrices que
            son en realidad universales!.</p>

        <CodeBlock language="javascript" code={$codeBlocks[0].code_javascript} header="Fuego" />
        <p>
            <Tag tag="Javascript" isCode={true} /> es el que decide cmo se van a comportar todos. Dirige el cotarro.
            Decide y determina cules sern las funciones o acciones que se van a llevar a cabo. En un sitio web se
            cumple su voluntad s o s.</p>
        <p>No es fcil de aprender pero su poder no tiene lmites. Es el fuego, es el pensamiento, la voluntad, el motor
            que mueve las cosas y el que las procesa. Su arquetipo es Marte, el guerrero y el rey con potestad para
            imbiscuirse en los asuntos de todos los dems.</p>

        <CodeBlock language="css" code={$codeBlocks[0].code_scss} header="Agua" />
        <p>La hoja de estilos se encarga del aspecto visual, el tono, la consistencia, la armona, el ritmo, las proporciones, la empata con el usuario, lo cognitivo, la interfaz grafica, el sabor. El
            <Tag tag="CSS" isCode={true} /> dota de identidad a los distintos elementos de la App.</p>
        <p>No por casualidad su elemento es el agua y el nmero dos, porque media entre lo de dentro y lo de afuera, entre el observador y lo observado.</p>

        <CodeBlock language="html" code={$codeBlocks[0].code_markup} header="Tierra" />
        <p>El
            <Tag isCode={true} tag="Html" />, o lenguaje de marcado, nos dar la base sobre la que se edifica todo. Es la estructura de nuestro sitio. Es el elemento que da cuerpo, que baja, cristaliza y posibilita las ideas.
            Es el sentido de realidad tan necesario. Al
            <Tag isCode={true} tag="Html" /> le interesa la practicidad, la accesibilidad. Es el lienzo donde todo ocurre y se manifiesta. Es el organizador, el soporte de las cosas.</p>

        <CodeBlock language="json" code="{$codeBlocks[0].code_json}" header="Aire" />
        <p>Y llegamos al contenido, al mensaje, el vnculo con el otro. Es el reino del lenguaje y la conexin. En la
            mitologa romana era representado por Mercurio y en la griega era Hermes. </p>

        <p>Uno de los formatos que ms se usa es
            <Tag tag="Json" isCode={true} />, un estndar para el intercambio de informacin. Innumerables webs se
            conectan a servicios API Rest que consumen datos. Tiene que ver con el arquetipo del mensajero "alado".</p>

        </Area>

        <Area>
        <p><strong>Cualquier ser, cualquier 'algo' requiere de estas 4 dimensiones:</strong></p>

        <List type="numbered">
            <li>Una mente o principio conceptual. Un propsito.</li>
            <li>Una piel sensible que lo contiene y le proporciona identidad</li>
            <li>Un vnculo y conexin con el mundo externo</li>
            <li>Un cuerpo o forma concreta que le de realidad y soporte</li>
        </List>

        <p>Cuatro dimensiones que 'van juntas' y determinan la Unidad. Y eso es lo nico que hay, la Unidad, el Uno y
            sus dvisiones o partes. Y no es algo me me haya inventado yo, lo deca Pitgoras cinco siglos antes de
            Cristo.</p>

        <p>Y aqu seores est toda la miga del asunto, entendida la naturaleza cuaternaria de un prototipo podemos ya
            pasar a una fase en la que vemos actuar todo esto en equilibrio y tambin cmo se anida en sistemas hacia
            arriba, en lo macro, y hacia adentro, en lo micro.</p>

        <p>Cmo lo hacemos? Hay miles de maneras distintas pero yo os voy a hablar de la que yo uso ahora mismo y la
            que mejor me funciona a mi.</p>

        <p>El mundo Front ha experimentado una gran evolucin y transformacin desde la poca de los 90 del siglo XX.
            Sus inicios fueron rudimentarios comparados con cmo est ahora el tema y las webs se caracterizaban por ser
            construidas 'a mano' por un tipo que saba de html, css, javascript, el antigo <em>Webmaster</em>. Desde
            ah una escalada exponencial creciente de herramientas, perfiles, metodologas fue dotando al Front de ms
            capacidad, especializacin, rapidez, etc pero tambin de mucha ms complejidad. Gestores de tareas fueron
            apareciendo para facilitar el trabajo de concatenar javascript, preprocesar css, hacer los builds, etc,
            aparecen gestores de dependencias como NPM, Bower, etc. Y fueron apareciendo libreras o frameworks como
            React, Angular, Vue... que aportan toda una serie de buenas cosas como la componentizacin, etc... </p>
        <p>El ecosistema de herramientas, mtodologas o formas de trabajar fue creciendo y hablar de ello es
            francamente para m inabarcable... Pero en esta inmensa torre de Babel del panorama front actual que ni
            puedo ni quiero abordar, hay una serie de propuestas, mtodos y herramientas que recuperan ese espritu
            artesanal de los 90 sin renunciar a muchos de los logros que se han conseguido por el camino.</p>

        <p>Personalmente simpatizo y me encuentro a gusto con ste Stack </p>

        <List type>
